
import * as React from 'react'
import { useColorMode, useDisclosure } from '@chakra-ui/react'

// 1. import `ChakraProvider` component
import { ChakraProvider, Heading } from '@chakra-ui/react'
import { Flex, Spacer } from '@chakra-ui/react'
import { Button, ButtonGroup } from '@chakra-ui/react'
import { Input } from '@chakra-ui/react'
import { Stack } from '@chakra-ui/react'
import { FormLabel, FormControl, FormHelperText } from '@chakra-ui/react'
import { useToast } from '@chakra-ui/react'


function App() {
  const { isOpen, onOpen, onClose } = useDisclosure()
  const toast = useToast()

  // 2. Wrap ChakraProvider at the root of your app
  return (
    <ChakraProvider>

      <Flex height="50vh" alignItems='center' justifyContent="center" gap='2'>
        <Flex direction="column" background="gray.50" p={12} rounded={8}>

          <Heading mb={6}>Sale Calculator</Heading>

          <Stack spacing={5}>
            <Stack spacing={3}>

              <FormControl>
                <FormLabel>Price</FormLabel>
                <Input id="price"></Input>
                <FormHelperText>The initial price of the product</FormHelperText>
              </FormControl>

              <FormControl>
                <FormLabel>Sale (%)</FormLabel>
                <Input id="sale"></Input>
                <FormHelperText>The percent off of the initial price</FormHelperText>
              </FormControl>
            </Stack>

            <ButtonGroup spacing='3'>
              <Button colorScheme='blue' onClick={() => toast({
                title: 'Result',
                description: document.getElementById("sale").value + "% off of " + document.getElementById("price").value + " is " + (document.getElementById("sale").value * document.getElementById("price").value) / 100,
                status: 'success',
                duration: 5000,
                isClosable: true,
              })}> Calculate</Button>

              <Button variant='outline' onClick={() => {
                document.getElementById("sale").value = ""
                document.getElementById("price").value = ""

                toast({
                  title: 'Cleared',
                  description: "Cleared all of the text boxes!",
                  status: 'info',
                  duration: 5000,
                  isClosable: true,
                })
              }}>Clear</Button>
            </ButtonGroup>
          </Stack>

        </Flex>

      </Flex>


    </ChakraProvider >
  )
}



export default App;
